.App {
  text-align: center;
}

.banner {
  background-color: var(--banner-background-color);
  color: white;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.image-container div {
  width: calc(100% / 3);
  margin: 10px auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.image-container p {
  font-size: 20px;
  font-weight: bold;
}

.image-container img {
  width: 75%;
  border-radius: 10px;
}

:root {
  --background-color: white;
  --text-color: black;
  --banner-background-color: #607d8b;
  --button-background-color: #607D8B;
}

@media (prefers-color-scheme: dark) {
  :root {
    --background-color: #1c1c1e;
    --text-color: white;
    --banner-background-color: #607D8B;
    --button-background-color: #607D8B;
  }
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
}

.reset-button {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 1em; /* you may adjust this based on your preference */
  padding: 10px 20px; /* reduced padding */
}

.image-icon {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
}

.banner h1 {
  display: flex;
  align-items: center;
}

.banner img {
  margin-right: 15px;
}

/* All media queries should be placed at the end */

@media (max-width: 600px) {
  .image-container {
    flex-direction: column;
    align-items: center;
  }

  .image-container div {
    width: 100%;
  }
  .reset-button {
    position: static;
    margin-top: 20px;
  }
}
